import React,{Component} from 'react';
import CSSModules from 'react-css-modules';
import logo from '../../assets/img/logo.png';
import styles from './styles/nav.css';

class Nav extends Component{
    render(){
        return (
            <div className='clearfix' styleName="root">
                <div className="lf" styleName="nav-logo">
                    <img src={logo} alt="logo" styleName="logo"/>
                    <span styleName="name">银海药店管理软件</span>
                </div>
                <div className="lf" styleName="nav-links">
                    <a href="" styleName="item">产品</a>
                    <a href="" styleName="item">成功案例</a>
                    <a href="" styleName="item">关于银海</a>
                    <a href="" styleName="item">联系我们</a>
                </div>
                <div className="rt" styleName="nav-btns">
                    <span styleName="btn">用户登录</span>
                    <span styleName="btn-active">申请入驻</span>
                </div>
            </div>
        )
    }
}
export default CSSModules(Nav,styles);


